<template>
  <div id="userList">
    <!--- 回收完成-->
    <el-dialog  :visible.sync="dialogVisible" width="430px" >
      <el-input  v-model="fromData.text"  placeholder="请输入回收地址"></el-input>
    <span slot="footer" class="dialog-footer" style="text-align: center">
      <el-button type="primary" size="medium" round @click="updateStatusPost()" style="width: 120px">确 定</el-button>
      <el-button size="medium" round @click="dialogVisible = false" style="width: 120px">取 消</el-button>
    </span>
    </el-dialog>
    <!--设备状态记录详情-->
    <el-dialog  :visible.sync="dialogVisible2" width="50%" >
      <div class="alertBox">
        <div class="card">
          <div class="clearfix"><div class="timeLabel left">2017-09-21 09:30</div> <div class="left"> 设备回收中</div><div class="cleae"></div></div>
          <div class="clearfix"><div class="timeLabel left"> </div><div><span class="city">北京</span> <span class="el-icon-back back"></span> <span class="city">深圳</span></div> <div class="cleae"></div></div>
        </div>
        <div class="card">
          <div class="clearfix"><div class="timeLabel left">2017-09-21 09:30</div> <div class="left"> 设备回收中</div><div class="cleae"></div></div>
          <div class="clearfix"><div class="timeLabel left"> </div><div><span class="city">北京</span> <span class="el-icon-back back"></span> <span class="city">深圳</span></div> <div class="cleae"></div></div>
        </div>
      </div>
      <div class="card" style="padding-top: 40px;">
        <div class="clearfix">
          <div class="timeLabel left">
            <el-button v-show="!fromShow" size="medium" round @click="fromShow = true" style="width: 120px">添加记录</el-button>
          </div>
        <div class="left">
           <div class="border" v-show="fromShow">
             <el-form :label-position="labelPosition" label-width="100px" :model="fromData" :rules="rules" ref="fromData">
               <el-form-item label="回收状态" prop="companyName" style="width: 500px">
                 <el-radio v-model="fromData.usageStatus" label="0">回收中</el-radio>
                 <el-radio v-model="fromData.usageStatus" label="1">回收完成</el-radio>
               </el-form-item>
               <el-form-item label="回收地点" prop="account" style="width: 500px">
                 <el-input v-model="fromData.text"></el-input>
               </el-form-item>
               <el-form-item label="" style="margin-bottom: 0px;">
                 <div style="text-align: right">
                   <el-button  round class="submit" style="width: 150px;" v-on:click="submitForm('fromData')"> 完成 </el-button>
                 </div>
               </el-form-item>
             </el-form>
           </div>
        </div>
        <div class="cleae"></div>
        </div>
      </div>
      <!--<span slot="footer" class="dialog-footer" style="text-align: center">-->
      <!--<el-button type="primary" size="medium" round @click="dialogVisible2 = false" style="width: 120px">确 定</el-button>-->
      <!--<el-button size="medium" round @click="dialogVisible2 = false" style="width: 120px">取 消</el-button>-->
      <!--</span>-->
    </el-dialog>
    <!--表格-->
    <div class="sech clearfix">
      <div class="left" style="width: 50%;margin-bottom: 25px;" :UserListData="UserListData">
        <el-input class="saechIput"  v-model="fromData.deviceNumber" placeholder="搜索设备编号"></el-input>
        <el-button type="primary" class="saechBout"  icon="el-icon-search" v-on:click="saechFn()"> </el-button>
      </div>
      <div class="right" style="width: 50%;text-align: right">
        <el-button size="medium" class="bot" icon="el-icon-delete">删除</el-button>
        <!--<el-button size="medium" class="bot" icon="el-icon-document">导出excel</el-button>-->
        <!--<el-button size="medium" type="primary" class="bot check" icon="el-icon-plus">批量导入</el-button>-->
      </div>
      <div class="tableBox" :DeviceListData="DeviceListData">
        <el-table :data="tableData2" style="width: 100%" :row-class-name="tableRowClassName" :row-style="style1"  :header-row-style="style2">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column prop="deviceNumber" label="设备号" width="150">
        </el-table-column>
        <el-table-column prop="model" label="设备型号" width="150">
        </el-table-column>
        <el-table-column prop="company" label="使用商家" >
        </el-table-column>
        <el-table-column prop="deviceStatusText" label="设备状态（电量）" width="200">
        </el-table-column>
        <el-table-column prop="usageStatusText" label="使用状态" >
        </el-table-column>
        <el-table-column prop="" label="设备所在地" >
          <template slot-scope="scope">
            <!--<span class="clickBot">-->
            <!--<span class="el-icon-edit-outline"></span>-->
            <!--<span>重置密码</span>-->
            <!--</span>-->
            <span class="clickBot"  v-on:click="updateDeviceUsageStatusFn(scope.row.id)" style="margin-left: 20px;">
               <a>详情</a>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="操作" >
          <template slot-scope="scope">
            <!--<span class="clickBot">-->
            <!--<span class="el-icon-edit-outline"></span>-->
            <!--<span>重置密码</span>-->
            <!--</span>-->
            <span class="clickBot el-icon-edit-outline" >
              <span style="padding-left:10px;" v-on:click="updateDeviceUsageStatusFn(scope.row.id,1)">回收完成</span>
            </span>
          </template>
        </el-table-column>
        </el-table>
      </div>
      <div class="cleae" style="text-align: right;padding-right: 20px;padding-top: 20px;">
        <el-pagination layout="prev, pager, next" :total="1000" @current-change="handleCurrentChange">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<style scoped>
  #userList{max-width: 1690px;padding-top: 25px;text-align: left}
  .saechIput{width: 392px;margin-right: 0px;position: relative;z-index: 10;border: none 0px}
  .saechBout{border-radius: 0px 4px 4px 0px;position: relative; left: -7px;z-index: 11;font-size: 14px;font-weight: bold;width: 60px;text-align: center}
  .saechBout:hover{background-color: #409eff;border-color: #409eff}
  .saechBout{background-color: #0e72b9; border-color: #0e72b9 }
  .bot{ width: 120px;text-align: center}
  .check{background-color: #0d71b7;margin-right: 60px;}
  .success-row {  background: #d9dddd;  }
  .tableBox{width: 100%;min-height: 200px;margin-top: 25px;}
  .clickBot{cursor: pointer;}
  .clickBot:hover{color:#409eff }
  .timeLabel{ width: 150px;display: block;height: 30px;}
  .back{ transform: rotateY(180deg);padding: 0px 10px}
  .city{ font-weight: bold}
  .card{padding: 0px 0px 10px 0px;}
  .border{ border: 1px dashed #e1e1e1;padding-bottom: 0px; padding: 10px;}
</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  import {mapState, mapActions} from 'vuex'
  export default {
    name: 'my-divseList',
    data () {
      return {
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        fromShow: false,
        userSaech: null,
        tableData2: [{
          id: '',
          deviceNumber: '',
          model: '',
          battery: '',
          deviceStatus: '',
          usageStatus: '',
          deviceStatusText: '',
          usageStatusText: '',
          company: '',
          location: '',
        }],
        style1: {height: '70px'},
        style2: {height: '70px', border: 'none 0px', background: '#d9dddd', 'margin-top': '25px'},
        fromData: {
          deviceId: null,
          usageStatus: '0',
          text: null
        },
        rules: {
          usageStatus: [
            { required: true, message: '请选择回收状态', trigger: 'blur' },
            { min: 2, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          text: [
            { required: true, message: '回收地点不能为空', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
        },
        dialogVisible: false, // 标记状态弹框
        dialogVisible2: false,// 详情弹框
        labelPosition: 'left'
      }
    },
    methods: {
      ...mapActions({
        updateDeviceUsageStatus: 'updateDeviceUsageStatus',
        getDeviceList: 'getDeviceList'
      }),
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      handleClose (a) {
          this.dialogVisible = true
      },
      handleClose2 (a) {
        this.dialogVisible2 = true
      },
      // 设备列表解析
      deviceListFn(res){
        res = res.body.data
        console.log(res)
        this.tableData2 = []
        for (let i in res) {
          var deviceStatus = '异常'
            ,usageStatus = '异常';
          if(res[i].deviceStatus == 0) {deviceStatus = '关机'}
          if(res[i].deviceStatus == 1) {deviceStatus = '开机'}
          if(res[i].usageStatus == 0) {usageStatus = '闲置中'}
          if(res[i].usageStatus == 1) {usageStatus = '使用中'}
          if(res[i].usageStatus == 2) {usageStatus = '回收中'}
          var pu = {
            id: res[i].id,
            deviceNumber: res[i].deviceNumber,
            model: res[i].model,
            battery: res[i].battery + '%',
            deviceStatus: res[i].deviceStatus,
            usageStatus: res[i].usageStatus,
            deviceStatusText: deviceStatus + '(' + res[i].battery + '%)',
            usageStatusText: usageStatus,
            company: res[i].company,
            location: res[i].location || '位置异常',
          }
          this.tableData2.push(pu)
        }
      },
      // 标记设备列表
      updateDeviceUsageStatusFn (id,status){
        console.log(id)
        this.fromData.deviceId = id
        if(status == 1){
          this.dialogVisible = true
          this.fromData.usageStatus = 0
        }else {
          this.dialogVisible2 = true
        }
      },
      // 修改标记
      updateStatusPost(){
        if(!this.fromData.text){
          var text = '备注不能为空'
          this.openError(text)
          return false
        }
        var parm = {
          deviceId: this.fromData.deviceId,
          status: this.fromData.usageStatus,
          text: this.fromData.text,
        }
        this.updateDeviceUsageStatus(parm)
      },
      // 登陆
      login () {
        this.$router.push({ name: "map" })
      },
      // 操作状态
      listStatus (res) {
        this.loading = false
        if (res.status == 0) {
          // 修改成功
          this.openSuccess(res.message)
          var parm = {
            companyName: '大洋运输',
            deviceNumber: this.fromData.deviceNumber
          }
          this.getDeviceList(parm)
        }
        if (res.status != 0) {
          // 修改成功 修改失败
          this.openError(res.message)
        }
        // 清空 vuex 中的本次记录，以免被下次操作状态造成混淆
        this.$store.commit('updateStatusTo', {body: null})
      },
      //
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.updateStatusPost()
//            var parm = {
//              companyId: this.fromData.companyId, // 商家名
//              account: this.fromData.account, // 账号
//              password: this.fromData.password // 密码
//            }
//            this.addUser(parm)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      }
    },
    computed: {
      DeviceListData: function () {
      var parm = this.DeviceList
      if (parm) {
        this.deviceListFn(parm)
      }
      return 1
    },
      UserListData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
  ...mapState([
    'updateStatus',
    'addOderData',
    'language',
    'DeviceList'
  ])
    },
    mounted () {
      // 配置顶部菜单
      var parm = {
        companyName: '大洋运输',
        deviceNumber: this.fromData.deviceNumber
      }
      this.getDeviceList(parm)
    },
    components: {
    }
  }
</script>




